var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"about-related"},[_c('navigate',{attrs:{"title":"关于","position":"absolute"}}),_vm._v(" "),_c('div',{staticClass:"scroll-wrapper"},[_c('div',{staticClass:"logo"},[_c('img',{attrs:{"src":_vm.logo}})]),_vm._v(" "),_c('div',{staticClass:"related-content"},[_c('h1',{staticClass:"title"},[_vm._v("广东航道通")]),_vm._v(" "),_c('div',{staticClass:"version"},[_vm._v("版本: "+_vm._s(_vm.version))]),_vm._v(" "),_vm._m(0),_vm._v(" "),_c('div',{staticClass:"APP-Information",on:{"click":function($event){_vm.agree = true}}},[_vm._v("\n                《广东航道通移动客户端用户使用协议》\n            ")])])]),_vm._v(" "),_c('mu-dialog',{attrs:{"title":"广东航道通移动客户端用户使用协议","width":"500px","max-width":"86%","open":_vm.agree,"scrollable":"","lock-scroll":""},on:{"update:open":function($event){_vm.agree=$event}}},[_c('div',{staticClass:"service-agreement"},[_c('p',[_vm._v("\n                广东航道通移动客户端（以下简称“广东航道通”）由广东省航道事务中心提供。在使用前，请认真阅读《广东航道通移动客户端用户使用协议》（以下简称“本协议”），确保您充分理解本协议中各条款。请您审慎阅读并选择接受或不接受本协议。除非您接受本协议所有条款，否则您无权下载、安装或使用本“广东航道通”及其相关服务。您一旦下载、安装、复制或以其它方式使用“广东航道通”产品，将视为对本“协议”的接受，即表示您同意接受本“协议”各项条款的约束。如果您不同意本“协议”中的条款，请勿下载、安装、复制或以其它方式使用广东航道通移动客户端软件。“本协议”是用户与广东航道事务中心之间关于用户下载、安装、使用、复制的法律协议。")]),_vm._v(" "),_c('h4',[_vm._v("一、总则")]),_vm._v(" "),_c('p',[_vm._v("1.1本协议双方为广东航道通与“广东航道通”的用户（以下简称“您”）。")]),_vm._v(" "),_c('p',[_vm._v("\n                1.2自您首次使用“广东航道通”时起，本协议即在广东省航道事务中心和您之间产生法律效力。本协议条款将构成对您具有约束力的法律文件，同时还表明您已完全同意并承认广东省航道事务中心已经履行了相关的提示和通知义务。")]),_vm._v(" "),_c('p',[_vm._v("1.3广东省航道事务中心有权修改或重新制定本协议，并通过公告形式告知您。您在公告之日后使用“广东航道通”的行为视为接受广东省航道事务中心对本协议的修改或重新制定。")]),_vm._v(" "),_c('h4',[_vm._v("二、使用“广东航道通”")]),_vm._v(" "),_c('p',[_vm._v("\n                2.1“广东航道通”由广东省航道事务中心研发，包括电子航道图显示和浏览、定位和助航、查询和搜索、AIS船舶显示和信息查询、船舶编组和管理、助航信息等相关的服务，具体服务内容由广东省航道事务中心按照“现状”向您提供。广东省航道事务中心有权自行确定“广东航道通”的形式和具体内容，广东省航道事务中心有权自行变更、中止或终止“广东航道通”的部分或全部服务，有权发布新版本并要求您使用。\n                "),_c('br'),_vm._v("\n                请您务必在确保安全的前提下使用“广东航道通”服务，以防发生事故或其他危险情况。“广东航道通”所提供的数据、信息及服务仅供参考，请不要以此作为船舶航行的唯一依据，在您行驶时以实际的航道状况为准，严格遵守相关法律法规。\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                2.2“广东航道通”的部分服务有账号注册功能，请您按照页面提示完成账号注册程序。其中的部分服务可能在注册账号后才能使用。在注册成功后，您将开通一个用户账号，请您自行负责保管。如果您已拥有账号，您可以通过账号直接登录。在账号注册过程中，您应当按照法律法规要求，根据页面提示准确提供账号注册所需个人资料，包括但不限于密码、手机号码等（统称为“个人资料”）。您保证您提供的个人资料均真实、完整、准确，并在注册资料发生任何变更时，及时更新最新资料。如有合理理由认为您提供的个人资料不实、不完整、不准确，或者未及时更新的，广东省航道事务中心有权向您询问并发出要求更正的通知，并有权自行删除相关资料，直至暂停、终止对您服务的部分或全部功能。您自行承担因此产生的全部后果和损失，广东省航道事务中心对此不承担责任。")]),_vm._v(" "),_c('p',[_vm._v("\n                请您自行妥善保管您的账号、密码及其他注册资料，并对您的用户账号下发生的一切行为承担责任。如果由于您未能妥善保管账号、密码及其他注册资料而使您自己、广东省航道事务中心、其他用户或任何第三方造成损害，您自行承担因此产生的全部后果和损失。")]),_vm._v(" "),_c('p',[_vm._v("\n                您所注册账号的所有权归属于广东省航道事务中心，使用权仅归属于您，除非有其他合法事由，并经过广东省航道事务中心同意，您不得将账号赠与、转让、售卖、借用或租用给他人。如果发生前述事项，您同意自行承担因此产生的全部后果和损失，广东省航道事务中心对此不承担责任，并保留追究相关行为人法律责任的权利。")]),_vm._v(" "),_c('p',[_vm._v("\n                如果发现您的账号存在未获授权的使用，或者其他可能危害您的账号安全的情形时，您应立即修改密码，并立即以有效方式通知广东省航道事务中心，要求广东省航道事务中心暂停相关服务。您理解广东省航道事务中心对您的请求采取行动需要合理时间，广东省航道事务中心对在采取行动前已经产生的后果（包括但不限于您的任何损失）不承担法律法规明确规定之外的责任。对于非因广东省航道事务中心的原因导致他人利用您的账号所进行的任何行为及因此而产生的后果或损失，请您向相应责任人索赔，广东省航道事务中心将提供必要的合理协助。")]),_vm._v(" "),_c('p',[_vm._v("\n                如果您遗失账号或遗忘密码，您应当按照广东省航道事务中心提示的途径申请找回账号或修改密码。您应当妥善保管注册资料与密码保护资料，如果由于您提供的资料不真实、不完整、不准确、不真实或由于广东省航道事务中心以外的原因导致您的资料被他人知悉，您可能无法找回账号或修改密码。广东省航道事务中心通过识别您所提供资料与系统保存资料的一致性进行判断，广东省航道事务中心不保证您一定可以通过前述方式找回账号或修改密码。")]),_vm._v(" "),_c('p',[_vm._v("\n                如您连续六个月未使用您的账号，广东省航道事务中心有权对该账号进行注销，您将不能再通过该账号登录或使用“广东航道通”的部分服务。")]),_vm._v(" "),_c('p',[_vm._v("\n                2.3为达到您使用“广东航道通”服务的目的，广东省航道事务中心在此授予您一项个人的、不可转让的、不可转授权的、非排他性的、可撤销的普通使用许可，以使得您能够以本协议许可的方式使用“广东航道通”相关服务。")]),_vm._v(" "),_c('p',[_vm._v("\n                本协议未明确的其他一切权利归广东省航道事务中心所有，本协议未授予您任何其他未明示的权利。如您使用其他权利，您需要及时联系广东省航道事务中心，事先获得广东省航道事务中心的书面许可，并与广东省航道事务中心另行签订书面协议。")]),_vm._v(" "),_c('h4',[_vm._v("三、用户行为要求")]),_vm._v(" "),_c('p',[_vm._v("\n                3.1您声明并保证，您对您通过“广东航道通”服务上报或以其它方式向广东省航道事务中心提供的任何信息，包括但不限于文字、